import { axios } from './utils/request'

export function postAction(url,parameter) {
    return axios({
        url: url,
        method:'post' ,
        data: parameter
    })
}
export function getPayDetail(parameter,headers) {
    return axios({
        url: '/api/otc/v1/order',
        method:'post',
        data: parameter,
        headers:{
            'ACCESS-KEY':'',
            'ACCESS-PASSPHRASE':'1',
            // 'ACCESS-TIMESTAMP':
            ...headers
        }
    })
}
export function pay(parameter,headers) {
    return axios({
        url: '/api/otc/v1/pay_order',
        method:'post',
        data: parameter,
        headers:{
            'ACCESS-KEY':'',
            'ACCESS-PASSPHRASE':'1',
            // 'ACCESS-TIMESTAMP':
            ...headers
        }
    })
}
