<template>
  <div style="text-align: center;height: 100%;overflow: auto">
    <div style="text-align: left;display: inline-block; width: 100%">
      <a-card>
        <div style="margin-bottom: 0.2rem;font-size: 0.2rem;text-align: center">
          订单号: {{orderDetail ? orderDetail.sOrderId :''}}
          <a-button v-if="orderDetail" size="small" v-clipboard:copy="orderDetail.sOrderId" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>
        </div>
        <a-result v-if="status !==''" :status="status">
          <div slot="title">
            {{ orderState }}
          </div>
          <div class="desc">
            <a-row :gutter="12">
              <a-col :span="8" class="row-title">支付方式:</a-col>
              <a-col :span="16"  class="row-content">
                {{initPayMode}}
              </a-col>
            </a-row>
            <a-row :gutter="12">
              <a-col :span="8" class="row-title">金额:</a-col>
              <a-col :span="16"  class="row-content amount">
                {{orderDetail ? "￥" + Number(orderDetail.dTradeAmount).toFixed(2) :''}}
                <a-button class="right" v-if="orderDetail && orderStateCode ===1" size="small" v-clipboard:copy="orderDetail.dTradeAmount" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>
              </a-col>
            </a-row>
            <!--              <a-row :gutter="16">-->
            <!--                <a-col :span="8" class="row-title">支付方式:</a-col>-->
            <!--                <a-col :span="16"  class="row-content">{{initPayMode}}</a-col>-->
            <!--              </a-row>-->
            <template v-if="payMode && payMode.iPayDictId ===15">
              <a-row :gutter="12" >
                <a-col :span="8" class="row-title">银行:</a-col>
                <a-col :span="16"  class="row-content">
                  {{payMode ? payMode.sBankName : ''}}
                  <a-button class="right" v-if="orderDetail && orderStateCode ===1" size="small" v-clipboard:copy="payMode.sBankName" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>
                </a-col>
              </a-row>
              <a-row :gutter="12" >
                <a-col :span="8" class="row-title">银行支行:</a-col>
                <a-col :span="16"  class="row-content">
                  {{payMode ? payMode.sSubbranchBankName : ''}}
                  <a-button class="right" v-if="orderDetail && orderStateCode ===1" size="small" v-clipboard:copy="payMode.sSubbranchBankName" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>
                </a-col>
              </a-row>
              <a-row :gutter="12" >
                <a-col :span="8" class="row-title">银行卡号:</a-col>
                <a-col :span="16"  class="row-content">
                  {{payMode ? payMode.sAccount : ''}}
                  <a-button class="right" v-if="orderDetail && orderStateCode ===1" size="small" v-clipboard:copy="payMode.sAccount" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>
                </a-col>
              </a-row>
              <a-row :gutter="12" >
                <a-col :span="8" class="row-title">账户名:</a-col>
                <a-col :span="16"  class="row-content">
                  {{payMode ? payMode.sName : ''}}
                  <a-button class="right" v-if="orderDetail && orderStateCode ===1" size="small" v-clipboard:copy="payMode.sName" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>

                </a-col>
              </a-row>
            </template>
            <template v-if="payMode && payMode.iPayDictId !==15">
              <a-row :gutter="12" >
                <a-col :span="8" class="row-title">收款账户名:</a-col>
                <a-col :span="16"  class="row-content">
                  {{payMode ? payMode.sName : ''}}
                  <a-button class="right" v-if="orderDetail && orderStateCode ===1" size="small" v-clipboard:copy="payMode.sName" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>
                </a-col>
              </a-row>
              <a-row :gutter="12" >
                <a-col :span="8" class="row-title">收款账号:</a-col>
                <a-col :span="16"  class="row-content">
                  {{payMode ? payMode.sAccount : ''}}
                  <a-button class="right" v-if="orderDetail && orderStateCode ===1" size="small" v-clipboard:copy="payMode.sAccount" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>
                </a-col>
              </a-row>
<!--              <template v-if="payMode && payMode.sAttachmentUrl && payMode.sAttachmentUrl !==''">-->
<!--                <div style="text-align: center">-->
<!--                  <img width="30%" :src="payMode.sAttachmentUrl"/>-->
<!--                  <div>付款二维码</div>-->
<!--                </div>-->
<!--              </template>-->
            </template>
          </div>
          <template slot="extra" v-if="orderStateCode ===1">
            <template>
              <p style="font-size: 0.25rem;color:red">
                请在 <span style="font-size: 0.35rem"> {{overtime}} </span> 时间内完成支付！！！
              </p>
              <span class="row-title">汇款人姓名：</span>
              <a-input placeholder="请输入汇款人姓名"  size="small" v-model="sRealName" style="display: inline-block; width: 1.5rem;margin: 0.15rem" />

              <a-button @click="confirmPayment" type="primary" size="small">付款完成</a-button>

              <div style="text-align: left;font-size: 0.2rem">
                <p>
                  <a-icon :style="{ color: 'red' }" type="info-circle" />
                  请填写正确的汇款人，以保证快速到账！
                </p>
                <p>
                  <a-icon :style="{ color: 'red' }" type="info-circle" />
                  请在有效时间内打款，否则可能无法到账！
                </p>
                <p>
                  <a-icon :style="{ color: 'red' }" type="info-circle" />
                  请按照上述金额正确支付，金额错误或重复打款将可能无法到账！
                </p>
                <p>
                  <a-icon :style="{ color: 'red' }" type="info-circle" />
                  付款后请点选“付款完成”，如15分钟内仍未到账，请立即联系客服！
                </p>
              </div>
            </template>
          </template>
        </a-result>
      </a-card>
    </div>
  </div>
</template>

<script>
import {PayMixin} from "./payMixin";
import {Button,Input,Card,Result,Row,Col,Icon} from 'ant-design-vue'
export default {
  name: "mobil",
  mixins:[PayMixin],
  components:{
    [Button.name]:Button,
    [Input.name]:Input,
    [Card.name]:Card,
    [Result.name]:Result,
    [Row.name]:Row,
    [Col.name]:Col,
    [Icon.name]:Icon,
  },
  created(){
    var abc = document.documentElement.clientWidth;

    var bl = abc/640//设计稿的宽度

    var fontSize = 100*bl

    document.documentElement.style.fontSize = fontSize + "px"

  }
}
</script>

<style lang="less" scoped>
 /deep/ .ant-card-body{
   padding:0.2rem;
   .ant-result{
     padding:0
   }
   .ant-result-title{
     font-size: 0.3rem;
   }
   .ant-result-content{
     padding: 0.2rem 0.2rem;
     margin-top: 0.2rem;
   }
   .row-title{

     font-size: 0.2rem;
     line-height: 0.5rem;
     font-weight: bold;
     margin-bottom: 0.1rem;
   }
   .row-content{
     font-size: 0.2rem;
     line-height: 0.5rem;
   }
   .amount{
     font-size: 0.35rem;
     color:red
   }
   .right{
     float: right;
   }
   .ant-result-extra{
     margin-top: 0.2rem;
   }
   p{
     margin-bottom: 0.1rem;
   }
   .ant-result-icon{
     margin-bottom: 0.2rem;

   }
   .ant-result-icon > .anticon{
     font-size: 1rem;
   }
 }
 /deep/ .ant-modal-body{
   padding: 0.4rem
 }
</style>