import {getPayDetail,pay} from '../api'
import moment from 'dayjs'
var utc = require('dayjs/plugin/utc')
moment.extend(utc)


export let PayMixin = {
    data(){
        return {
            id:'',
            tk:'',
            date:moment().utc().format(),
            orderDetail:null,
            payMode:null,
            sRealName:'',
            overtime:'',
            isOverTime:null,
            iCurrentTime:0,
            iExpireTime:0,
            getOrderDetailInterval:null,
        }
    },
    computed:{
        initPayMode(){
            let payMode = ''
            if(this.payMode){
                switch (this.payMode.iPayDictId){
                    case 13:
                        payMode = '支付宝'
                        break
                    case 14:
                        payMode = '微信'
                        break
                    case 15:
                        payMode = '银行卡'
                        break
                }
            }
            return payMode
        },
        orderStateCode(){
            return this.orderDetail ? this.orderDetail.iOrderState : null
        },
        status(){
            let status = ''
            if(this.orderDetail){
                switch (this.orderDetail.iOrderState){
                    case 1:
                        status = 'info'
                        break
                    case 2:
                        status = 'success'
                        break
                    case 3:
                        status = 'warning'
                        break
                    case 4:
                        status = 'success'
                        break
                    case 10:
                        status = 'success'
                        break
                    case 11:
                        status = 'error'
                        break
                    case 12:
                        status = 'error'
                        break
                }
            }
            return status
        },
        orderState(){
            let orderState = ''
            if(this.orderDetail){
                switch (this.orderDetail.iOrderState){
                    case 1:
                        orderState = '等待支付'
                        break
                    case 2:
                        orderState = '已支付，请耐心等待财务确认'
                        break
                    case 3:
                        orderState = '申诉中'
                        break
                    case 4:
                        orderState = '已支付，请耐心等待财务确认'       //放款超时
                        break
                    case 10:
                        orderState = '充值完成'
                        break
                    case 11:
                        orderState = '订单已取消'
                        break
                    case 12:
                        orderState = '支付超时，订单已取消'
                        break
                }
            }
            return orderState
        }
    },
    created(){
        this.id = this.$route.query.id
        this.tk = this.$route.query.tk
        const params = {
            sOrderId:this.id
        }
        const headers = {
            'ACCESS-TIMESTAMP':moment().utc().format(),
            'ACCESS-SIGN':this.tk
        }
        getPayDetail(params,headers)
            .then(res=>{
                if(res.code === 0){
                    this.orderDetail = res.data.stOrderData
                    this.iCurrentTime = res.data.iCurrentTime
                    this.iExpireTime = this.orderDetail.iExpireTime - this.iCurrentTime
                    this.payMode = res.data.vUsedList[0]
                    this.overTimeInterval()
                }
                console.log(res)
            })
    },
    methods:{
        confirmPayment(){
            if(this.sRealName ===''){
                this.$message.error('请填写汇款人姓名')
                return
            }
            this.$confirm({
                title:'确认支付',
                content:'您确认已完成付款了吗？',
                okText:'我已确认',
                cancelText:'还没有',
                onOk:()=>{
                    const params = {
                        sRealName:this.sRealName,
                        sOrderId:this.id
                    }
                    const headers = {
                        'ACCESS-TIMESTAMP':moment().utc().format(),
                        'ACCESS-SIGN':this.tk
                    }
                    pay(params,headers)
                        .then(res=>{
                            if(res.code === 0){
                                getPayDetail(params,headers)
                                    .then(res=>{
                                        // debugger
                                        this.orderDetail = res.data.stOrderData
                                        this.iCurrentTime = res.data.iCurrentTime
                                        this.iExpireTime = this.orderDetail.iExpireTime - this.iCurrentTime
                                    })
                            }
                        })
                }
            })
        },
        overTimeInterval(){
            if(this.iExpireTime <= 0){
                this.isOverTime = true
                const params = {
                    sOrderId:this.id
                }
                const headers = {
                    'ACCESS-TIMESTAMP':moment().utc().format(),
                    'ACCESS-SIGN':this.tk
                }
                getPayDetail(params,headers)
                    .then(res=>{
                        // debugger
                        this.orderDetail = res.data.stOrderData
                        this.iCurrentTime = res.data.iCurrentTime
                        this.iExpireTime = this.orderDetail.iExpireTime - this.iCurrentTime
                    })
                return
            }
            // debugger
            let m = parseInt(this.iExpireTime/60000).toString()
            let s = (parseInt((this.iExpireTime%60000)/1000)).toString()
            if(m.length === 1)m ='0' + m
            if(s.length === 1)s ='0' + s
            this.overtime =  m + ' : ' + s
            this.iExpireTime -=1000
            setTimeout(()=>this.overTimeInterval(),1000)
        },
    }
}
