<template>
    <div style="text-align: center">
      <div style="width: 800px;text-align: left;display: inline-block;margin-top: 0.5rem">
        <a-card title="收银台">
          <div slot="extra">
            订单号: {{orderDetail ? orderDetail.sOrderId :''}}
            <a-button v-if="orderDetail" size="small" v-clipboard:copy="orderDetail.sOrderId" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>
          </div>
          <a-result v-if="status !==''" :status="status">
            <div slot="title">
              {{ orderState }}
            </div>
            <div class="desc">
              <a-row :gutter="16">
                <a-col :span="8" class="row-title">支付方式:</a-col>
                <a-col :span="16"  class="row-content">
                  {{initPayMode}}
                </a-col>
              </a-row>
              <a-row :gutter="16">
                <a-col :span="8" class="row-title">金额:</a-col>
                <a-col :span="16"  class="row-content amount">
                  {{orderDetail ? "￥" + Number(orderDetail.dTradeAmount).toFixed(2) :''}}
                  <a-button v-if="orderDetail && orderStateCode ===1" size="small" v-clipboard:copy="orderDetail.dTradeAmount" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>
                </a-col>
              </a-row>
<!--              <a-row :gutter="16">-->
<!--                <a-col :span="8" class="row-title">支付方式:</a-col>-->
<!--                <a-col :span="16"  class="row-content">{{initPayMode}}</a-col>-->
<!--              </a-row>-->
              <template v-if="payMode && payMode.iPayDictId ===15">
                <a-row :gutter="16" >
                  <a-col :span="8" class="row-title">银行:</a-col>
                  <a-col :span="16"  class="row-content">
                    {{payMode ? payMode.sBankName : ''}}
                    <a-button v-if="orderDetail && orderStateCode ===1" size="small" v-clipboard:copy="payMode.sBankName" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>
                  </a-col>
                </a-row>
                <a-row :gutter="16" >
                  <a-col :span="8" class="row-title">银行支行:</a-col>
                  <a-col :span="16"  class="row-content">
                    {{payMode ? payMode.sSubbranchBankName : ''}}
                    <a-button v-if="orderDetail && orderStateCode ===1" size="small" v-clipboard:copy="payMode.sSubbranchBankName" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>
                  </a-col>
                </a-row>
                <a-row :gutter="16" >
                  <a-col :span="8" class="row-title">银行卡号:</a-col>
                  <a-col :span="16"  class="row-content">
                    {{payMode ? payMode.sAccount : ''}}
                    <a-button v-if="orderDetail && orderStateCode ===1" size="small" v-clipboard:copy="payMode.sAccount" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>

                  </a-col>
                </a-row>
                <a-row :gutter="16" >
                  <a-col :span="8" class="row-title">账户名:</a-col>
                  <a-col :span="16"  class="row-content">
                    {{payMode ? payMode.sName : ''}}
                    <a-button v-if="orderDetail && orderStateCode ===1" size="small" v-clipboard:copy="payMode.sName" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>

                  </a-col>
                </a-row>
              </template>
              <template v-if="payMode && payMode.iPayDictId !==15">
                <a-row :gutter="16" >
                  <a-col :span="8" class="row-title">收款账户名:</a-col>
                  <a-col :span="16"  class="row-content">
                    {{payMode ? payMode.sName : ''}}
                    <a-button v-if="orderDetail && orderStateCode ===1" size="small" v-clipboard:copy="payMode.sName" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>
                  </a-col>
                </a-row>
                <a-row :gutter="16" >
                  <a-col :span="8" class="row-title">收款账号:</a-col>
                  <a-col :span="16"  class="row-content">
                    {{payMode ? payMode.sAccount : ''}}
                    <a-button v-if="orderDetail && orderStateCode ===1" size="small" v-clipboard:copy="payMode.sAccount" v-clipboard:success="()=>$message.success('复制成功')" v-clipboard:error="()=>$message.error('复制失败')" style="margin-left: 16px">复制</a-button>
                  </a-col>
                </a-row>
                <a-row :gutter="16" v-if="payMode && payMode.sAttachmentUrl && payMode.sAttachmentUrl !==''">
                  <a-col :span="8" class="row-title">付款二维码:</a-col>
                  <a-col :span="16"  class="row-content">
                    <img width="50%" :src="payMode.sAttachmentUrl"/>
                  </a-col>
                </a-row>
              </template>
            </div>
            <template slot="extra" v-if="orderStateCode ===1">
              <template>
                <p style="font-size: 0.3rem;color:red">
                  请在 <span style="font-size: 0.4rem">{{overtime}}</span> 时间内完成支付！！！
                </p>
                <div style="text-align: left;">
                  <p>
                    <a-icon :style="{ color: 'red' }" type="info-circle" />
                    请在有效时间内打款，否则可能无法到账！
                  </p>
                  <p>
                    <a-icon :style="{ color: 'red' }" type="info-circle" />
                    请按照上述金额正确支付，金额错误或重复打款将可能无法到账！
                  </p>
                  <p>
                    <a-icon :style="{ color: 'red' }" type="info-circle" />
                    付款后请在下方点选“付款完成”，如15分钟内仍未到账，请立即联系客服！
                  </p>

                  <span class="row-title">汇款人姓名：</span>
                  <a-input placeholder="请输入汇款人姓名" v-model="sRealName" style="display: inline-block; width: 150px;margin: 12px" />
                  <a-button @click="confirmPayment" type="primary">付款完成</a-button>
                  <p>
                    <a-icon :style="{ color: 'red' }" type="info-circle" />
                    请填写正确的汇款人，以保证快速到账！
                  </p>
                </div>

              </template>

            </template>
          </a-result>



        </a-card>
      </div>
    </div>
</template>

<script>
import {PayMixin} from "./payMixin";
    import {Button,Input,Card,Result,Row,Col,Icon} from 'ant-design-vue'

export default {
name: "web",
  mixins:[PayMixin],
  components:{
    [Button.name]:Button,
    [Input.name]:Input,
    [Card.name]:Card,
    [Result.name]:Result,
    [Row.name]:Row,
    [Col.name]:Col,
    [Icon.name]:Icon,
  },

  created(){
    document.documentElement.style.fontSize = "80px"
  }
}
</script>

<style lang="less" scoped>
  .desc{

  }
  .row-title{
    text-align: right;
    font-size: 0.2rem;
    line-height: 0.5rem;
    font-weight: bold;
  }
  .row-content{
    font-size: 0.2rem;
    line-height: 0.5rem;
  }
  .amount{
    font-size: 0.35rem;
    color:red
  }
</style>